path#trail {
  stroke-dasharray: 8;
  stroke-dashoffset: 0;
  stroke: white;
  animation: dash 10s linear infinite forwards;
}

.playButton {
  transition: 0.5s;
}
path#trailright {
  stroke-dasharray: 8;
  stroke-dashoffset: 100%;
  stroke: white;
  animation: dashright 10s linear infinite forwards;
}

@keyframes dash {
  to {
    stroke-dashoffset: 100%;
  }
}

@keyframes dashright {
  to {
    stroke-dashoffset: 0%;
  }
}
